$background: url(../photos/background.jpeg) 100%;
$font: "Inter", sans-serif;
$maincolor: #ccff15;


html, body{
  background: $background;
  width: 100vw;
  height: 100vh;
  background-size:cover;
  display:flex;
  justify-content: center;
  align-items:center;
  -webkit-tap-highlight-color:transparent;
  user-select:none;
  -webkit-user-select: none;
  -moz-user-select: none; 
  -ms-user-select: none; 
}

a {
  text-decoration:none;
  color:black;
}


%main_circle {
  border-radius: 50%;
  background: $maincolor;
  opacity:0.5;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
  cursor:pointer;
  font-family: $font; 
}

.container_hola-about{
  @extend %main_circle;
  width:250px;
  height:250px;
  font-size: 46px;
}


.footer{
  &_hi{
  position: fixed;
  bottom: 5px;
  left: 49%;
  font-size: 12px;
  text-transform: uppercase;
  font-family: "Inter";
  color:rgb(47, 49, 47);
  }

  @media (max-width: 581px) {
    .footer_hi{
      left: 48%;
    }
  }
}

