$font: "Inter", sans-serif;
$maincolor: #ccff15;

%main_circle {
    border-radius: 50%;
    background: $maincolor;
    opacity:0.5;
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
    cursor:pointer;
    font-family: $font; 
  }

.container_home_collumn {
    &_first  {
      float:left
    }
  
    &_second {
      float:right;
    }
  
    &_first-who, &_first-why, &_second-photos, &_second-contact{
      @extend %main_circle;
      width:160px;
      height:160px;
      margin: 10px;
      text-transform: uppercase;
      font-size: 15px;
    }
  
    @media (max-width: 531px) {
      &_first-who, &_first-why, &_second-photos, &_second-contact{
        width:130px;
        height:130px;
      }
    }
  }      
  