$font: "Inter", sans-serif;
$maincolor: #ccff15;

%main_circle {
    border-radius: 50%;
    background: $maincolor;
    opacity:0.5;
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
    cursor:pointer;
    font-family: $font; 
  }
  
.container_square{
    &_photos-box > img{
      height:382px;
    }
    
    &_photos{
      float:left;
      width:382px;
      height:500px;
      background-color: #ccff1580;  
      padding: 10px;
      user-select:none;
  
      &-prev, &-next{
        cursor: pointer;
        font-size: 20px;
        justify-content: center;
        outline: none !important;
        box-shadow: none;
        background-color: transparent;
        border-color: transparent;
        opacity:0.5;
        color:grey;
        top: 80%;
        padding:10px;
      }
  
      &-prev{
        margin-left: 158px;
      }
    } 
  
    @media (max-width: 531px){
      .container_square{
        &_photos-box > img{
          height:260px;
        }
  
        &_photos{
          float:none;
          width:260px;
          height:300px;
          background-color: #ccff1580;
          margin: 0 auto;
  
          &-prev, &-next {
            color:grey;
            font-size: 15px;
            top:5%;
            padding:5px;
            -webkit-tap-highlight-color:transparent;
          }
  
          &-prev {
            margin-left: 110px;
          }
        }
      }
    }
    
    &_other{
      float:right;
      margin: 96px 0px;
  
      &-who, &-why, &-contact{
        @extend %main_circle;
        width:100px;
        height:100px;
        margin:7px;
        text-transform: uppercase;
        font-size:14px;
      }
    }
  
    @media (max-width: 531px){
      .container_square_other{
        float:none;
        margin: 0 auto;
        margin-bottom:18px;
        margin-left:2px;
  
        &-why, &-contact, &-who {
          display:inline-block;
          margin:10px;
          width:80px;
          height:80px;
          font-size:12px;
          line-height: 80px;
        }
      }
    }
  }  
  