$font: "Inter", sans-serif;
$maincolor: #ccff15;

%main_circle {
    border-radius: 50%;
    background: $maincolor;
    opacity:0.5;
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
    cursor:pointer;
    font-family: $font; 
  }

.container_circle{
    &_about-why, &_about-who, &_about-contact {
      @extend %main_circle;
      width:400px;
      height:400px;
      border: 1px solid $maincolor;
      float:left;
      font-size: 12px;
    }
  
    &_about-who {
      opacity:0.7;
      background-image: url(../photos/me.jpeg);
      background-size:contain;   
      
      &_info {
        width: 200px;
        height: 50%;
        padding:33%;
        color:white;  
      }
    }
  
    &_about-why_info{
      padding:15px;
    } 
  
    &_about-contact {
      user-select: all;
    }
  
   
    
    @media(max-width: 531px){ 
      .container_circle{
        &_about-contact, &_about-why, &_about-who {
          width:300px;
          height:300px;
          float:none;
          margin: 40px 0px -40px 0px;
          justify-content: center;
      
          &_me, &_info {
            font-size: 11px;
          }
        }
      }
    }
  
  
    &_list{
      float:right;
      margin: 40px 0px; 
  
      &-why, &-photos, &-contact, &-who {
        @extend %main_circle;
        width:100px;
        height:100px;
        margin:7px;
        text-transform: uppercase;
        font-size:14px;
        font-family: $font;
      } 
  
      @media (max-width: 531px){
        .container_circle{
          &_list {
            float:none;
  
            &-why, &-photos, &-contact, &-who {
              display:inline-block;
              margin:10px;
              width:80px;
              height:80px;
              font-size:12px;
              text-align:center;
              vertical-align:middle;
              line-height: 80px;
            }
          }
        }
      }
    }
  }
  